import React, { Component } from 'react'
import '../css/cities.css'; 

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import { RadioGroup, Collapse} from '@material-ui/core';

import {connect} from 'react-redux'
import {change_dialog} from '../_actions/Dialog'

class ListCities extends Component {
    constructor(props){
        super(props);
        this.state = {
            open: false,
            setOpen: false,
            text: '',
            cities:[
                { label: 'Bogot',value: 'bogot' },
                { label: 'Santa Marta',value: 'santa marta'},
                { label: 'Barranquilla',value: 'barranquilla'},
                { label: 'Cartagena',value: 'cartagena'},
                { label: 'La Dorada',value: 'dorada'},
                { label: 'Belencito',value: 'belencito'},
            ],
            name:{
                'bogot':"Bogot",
                'barranquilla': 'Barranquilla',
                'cartagena':'Cargtagena',
                'dorada':'La Dorada',
                'santa marta':'Santa Marta',
            },
            checked: ''
        }
        this.OpenList = this.OpenList.bind(this);
        this.ChangeOption = this.ChangeOption.bind(this);

    }
    OpenList(){
        this.setState({open: !this.state.setOpen, setOpen: !this.state.open})
        
    }

    async ChangeOption(event){
        let option = event.target.value
        this.props.change_dialog([this.props.type,option]);
        this.setState({ text: this.state.name[option] })
        this.props.change_dialog(['checked',option])
    }
    
    render(){
        
        const {dialog} = this.props
        let cities = this.state.cities.map((city)=>{
            if (dialog.checked !== city.value && city.value !== dialog.origin && city.value !== dialog.destination){
                return(
                    <ListItem>
                        <FormControlLabel 
                            control={ 
                                <Radio 
                                    className="list" 
                                    onChange={this.ChangeOption} 
                                    disabled={false}
                                    value={city.value}/>
                            }
                            label={city.label}/>
                    </ListItem>
                )
            }
        })

        return(
            <List justify="center" style={{color:this.props.color}}>
                
                <ListItem button onClick={this.OpenList}>
                <ListItemText primary={
                        this.state.text === '' ? 
                        this.props.type : 
                        this.props.type +" : "+this.state.text
                    }/>
                    {this.state.open ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                    <RadioGroup>
                        {cities}
                    </RadioGroup>
                </Collapse>
            </List>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        dialog: state.Dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
       change_dialog: (value) => {dispatch(change_dialog(value))},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ListCities);