export const defaultState = {
    checked: '',
    open: false,
    cards: [
        /*{   
            id: 1,
            title: 'hallo',
            class: 'fdafd',
            transport: 'fasdfasdf',
            origin: 'dsadfasf',
            destination: 'sadfasdf',
            date_start: 'sdafasdfadsfadfadfs',
            date_end: 'sdafasdfadsfadfadfs',
        }*/
    ],
    openN: false,
    openES: false,
    openI: false,
    id_service:  0,
    title: '',
    type: 'carga',
    capacity: 0,
    places: 0,
    cost: '',
    cities: [
        {name:'Bogot' ,lat: 4.6097100, lng: -74.0817500 ,option: false},
        {name:'Santa Marta' ,lat: 11.2407900, lng: -74.1990400 ,option: false},
        {name:'Barranquilla' ,lat: 10.9685400, lng: -74.7813200 ,option: false},
        {name:'Cartagena' ,lat: 10.3997200, lng: -75.5144400 ,option: false},
        {name:'La dorada' ,lat: 5.4478302, lng: -74.6631088 ,option: false},
        {name:'Belencito' ,lat: 6.2518400, lng: -75.5635900 ,option: false},
    ],
    origin: '',
    destination: '',
    transport: '',
    service_created: false,
    service_updated: false,
    visibility: false,
    dateStart: new Date(),
    dateEnd: new Date()
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'OPEN':
            //console.log("entry in dialog open")
            return{ ...state,open: !state.open}
        case 'OPENDN':
            return{...state,openN: !state.openN}
        case 'OPENES':
            return{...state,openES: !state.openES}
        case 'OPENDI':
            return{...state,openI: !state.openI}
        case 'CHANGE_DIALOG':
            switch(payload[0]){
                case 'id_service':
                    return {...state,id_service:payload[1]}
                case 'cards':
                    return {...state,cards:payload[1]}
                case 'checked':
                    return {...state,checked:payload[1]}
                case 'title':return {...state,title:payload[1]}
                case 'capacity':return {...state,capacity:parseInt(payload[1])}
                case 'places':return {...state,places:parseInt(payload[1])}
                case 'cost':return {...state,cost:payload[1]}
                case 'Transporte':return {...state,transport:payload[1]}
                case 'Origen':return{...state,origin:payload[1]}
                case 'Destino':return{...state,destination:payload[1]}
                case 'salida':return{...state, dateStart: payload[1]}
                case 'visibility': return{...state, visibility: !state.visibility}
                case 'service_created': return{...state, service_created: !state.service_created}
                case 'service_updated': return{...state, service_updated: !state.service_updated}
                default:return{...state,dateEnd: payload[1]}
            }
        default:
            return state
            
    }
    
}

export default reducer;