import API_ROUTE from '../constants/api_route'
import store from '../store'


const axios = require("axios")


// open services dialog 
export const open = (option) =>{
    return {
        type: 'OPEN',
        payload: option
    }
}

// open notifications dialog 
export const openDN = (option) =>{
    return {
        type: 'OPENDN',
        payload: option
    }
}

// open information dialog 
export const openDI = (option) =>{
    return {
        type: 'OPENDI',
        payload: option
    }
}

// open edit_service dialog 
export const openES = (option) =>{
    return {
        type: 'OPENES',
        payload: option
    }
}


// change any value in dialog
export const change_dialog = (option) =>{
    return {
        type: 'CHANGE_DIALOG',
        payload: option
    }
}

// Create a service 
export const create = () =>{
    return (dispatch) => {
        //setTimeout(() => {}, 2000);
        let state = store.getState().Dialog
        console.log(state)
        axios.post(API_ROUTE,{
            query : `mutation{
                createService(Service: {
                    id_user: ${localStorage.getItem('id_user')}
                    title: "${state.title}"
                    class: "${state.type}"
                    capacity: ${state.capacity}
                    places: ${state.places}
                    cost: "${state.cost}"
                    origin: "${state.origin}"
                    destination: "${state.destination}"
                    transport: "${state.transport}"
                    visibility: ${state.visibility}
                    date_start: "${state.dateStart}"
                    date_end: "${state.dateEnd}"
                }){
                    id
                    title
                }
            }`
        }).then((result)=>{
         
            const title = result.data.data.createService.title
            if(title !== " "){
                dispatch(change_dialog(['service_created','any']))
            }else{
                alert("No se creo el servicio")
            }
        }).catch(err => console.log(err))
        
    }
}

// update a service 
export const update = (id_service) =>{
    return (dispatch) => {
        let state = store.getState().Dialog
        console.log(id_service)
        //setTimeout(() => {}, 2000);
        console.log(state)
        axios.post(API_ROUTE,{
            query : `mutation{
                updateService(id: ${id_service},Service:{
                    places: ${state.places}
                    date_start: "${state.dateStart}"
                    date_end: "${state.dateEnd}"
                }){
                    places
                }
              }`
        }).then((result)=>{
         
            const places = result.data.data.updateService.places
            if(places === state.places){
                dispatch(change_dialog(['service_updated','any']))
            }

        }).catch(err => console.log(err))
        
    }
}

// Load all services
export const load = () =>{
    return(dispatch)=> {
        axios.post(API_ROUTE, {
            query: `query{
                getServices{
                    id 
                    id_user 
                    title 
                    class 
                    capacity
                    places 
                    cost 
                    origin 
                    destination 
                    transport 
                    date_start
                    date_end
                }
            }`
            
        }).then((result)=>{
            let service  = result.data.data.getServices
            dispatch(change_dialog(['cards',service]))
            //console.log("service: ", state.cards)
        }).catch(err => console.log(err))
    } 
}
