import React,{Component}   from 'react';
 
import BackDrop from '../components/BackDrop'
import Button from '@material-ui/core/Button';
import DateTime from './DateTime'
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Fab from '@material-ui/core/Fab';
import { Grid } from '@material-ui/core';
import Slider from '@material-ui/core/Slider';
import TextField from '@material-ui/core/TextField';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';

import {connect} from 'react-redux'
import {openBD} from '../_actions/actions_backdrop' 
import {change_dialog,openES,update} from '../_actions/Dialog'
import { openSnack } from '../_actions/actions_snack'

class EditService extends Component{
    constructor(props) {
        super(props)
        this.state = {
            message_backdrop: 'Actualizando servicio..',
        }
    }
    
    closeDialog(){
        this.props.openES('close')
    }
    
    ValueLabelComponent(props) {
        const { children, open, value } = props;
      
        return (
          <Tooltip open={open} enterTouchDelay={0} placement="top" title={value}>
            {children}
          </Tooltip>
        );
    }

    changeState(event){
        
        let {id , value} = event.target
        console.log("change state ",id, value)
        //this.props.change_dialog([id,value])
    }

    changeSlider(event, value){
        setTimeout(() => {
            this.props.change_dialog(['places',value])    
        }, 2000);
    }

    //update service
    updateService(){
        
        // open backdrop
        this.props.openbd('open')
        setTimeout(() => {
            // send request to update service
            let { dialog } = this.props;
            this.props.update(dialog.id_service);
        }, 1000);

        setTimeout(() => {
            // verify if the service was updated
            let { dialog } = this.props;
            console.log(dialog.service_updated)
            if(dialog.service_updated){
                this.props.open_snack('snack_alert')
                setTimeout(() => { this.props.open_snack('snack_alert');}, 4000);
            }
            this.props.openbd('console.log')
            this.props.openES('close')
        }, 4000);

        
    }

    render(){
        
        var values = 'hola,0'
        let { dialog } = this.props;
        let { cards } = dialog
        let capacity = cards.map((card) => {
            if (parseInt(card.id) === parseInt(dialog.id_service)){
                let { places} = card
                return places
            }else{ return 0}
        })
        
        values = capacity[dialog.id_service-1]
        

        return(
            <Dialog open={dialog.openES}>
                <BackDrop message={this.state.message_backdrop}/>
                <DialogTitle >
                    <Grid container direction="row">
                        <Grid item xs={5} md={5}>{"Editar Servicio"}</Grid>
                        <Grid item xs={3} md={3}></Grid>
                        <Grid item xs={4} md={4} align="right">
                            <Fab color="primary" size="small" >
                                <ExitToAppIcon onClick={this.closeDialog.bind(this)}/>
                            </Fab>
                        </Grid>
                    </Grid>
                </DialogTitle>
                <DialogContent>
                    
                    <Grid container direction={'row'}>
                        <Grid item xs={6}>
                            <DateTime labelDate={"Fecha Salida"} labelTime={"Hora Salida"}/>
                        </Grid>
                        <Grid item xs={6}>
                            <DateTime labelDate={"Fecha Llegada"} labelTime={"Hora Llegada"}/>        
                        </Grid>
                    </Grid>

                    <Grid container style={{paddingTop: '30px'}} spacing={3} direction={'row'}>
                        <Grid item xs={4}>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                disabled={true}
                                fullWidth
                                value={30 - values + ' plataformas'}
                                id="capacity"
                                label="Espacios libres"
                                autoFocus
                                //onChange={this.changeState.bind(this)}
                            />
                        </Grid>
                        <Grid item xs={8} align="center">
                            
                            <Slider
                                ValueLabelComponent={this.ValueLabelComponent.bind(this)}
                                //aria-label="custom thumb label"
                                defaultValue={values}
                                min={values}
                                max={30}
                                onChange={this.changeSlider.bind(this)}
                                style={{paddingTop: '20px'}}
                            />
                            <Typography gutterBottom>
                                Mueva el slider para cambiar el valor de los
                                espacios libres
                            </Typography>   
                        </Grid>
                    </Grid>
                    <Grid container style={{paddingTop: '30px'}} spacing={3} direction={'row'}>
                        <Grid item xs={4} align="center">
                            <Button 
                                fullWidth
                                variant="outlined"
                                color="inherit"
                                onClick={this.handleReset}
                                className="button"
                                >
                                Cancelar
                            </Button>
                        </Grid>
                        <Grid item xs={4} align="center"></Grid>
                        <Grid item xs={4} align="center">
                            <Button 
                                fullWidth
                                variant="contained"
                                color="primary"
                                onClick={this.updateService.bind(this)}
                                className="button"
                                >
                                Actualizar
                            </Button>
                        </Grid>
                    </Grid>

                    
                </DialogContent>
            </Dialog>
        );
    }
}

const mapStateToProps = (state) => {
    return {
       dialog: state.Dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        change_dialog: (date) => {dispatch(change_dialog(date))},
        openbd: (text) => {dispatch(openBD(text))},
        openES: (option) => {dispatch(openES(option))},
        update: (option) => {dispatch(update(option))},
        open_snack: (text) => {dispatch(openSnack(text))},
    }
}
 
export default connect(mapStateToProps,mapDispatchToProps)(EditService);