import React, { Component } from 'react'
import '../css/navbar.css'; 

import AppBar from '@material-ui/core/AppBar'
import AttachFileIcon from '@material-ui/icons/AttachFile';
//import auth from '../router/Auth'
import AccountCircle from '@material-ui/icons/AccountCircle';
import Badge from '@material-ui/core/Badge';
import CloudQueueIcon from '@material-ui/icons/CloudQueue';
//import Fab from '@material-ui/core/Fab';
import IconButton from '@material-ui/core/IconButton'
//import Info from '../resources/question.svg'
import MenuNav from '../components/MenuNav'

import MenuIcon from '@material-ui/icons/Menu';

import NotificationsIcon from '@material-ui/icons/Notifications';
import Search from '../resources/question.svg'
import Toolbar from '@material-ui/core/Toolbar'
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography'
import {withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/styles';


import {connect} from 'react-redux'
import {open_drawer} from '../_actions/Drawer'
import {change} from '../_actions/Register'
import {open_menu,change_menu} from '../_actions/actions_navbar'
import {openDI} from '../_actions/Dialog'

const styles = theme => ({
    grow: {
        flexGrow: 1,
    },
    sectionDesktop: {
        display: 'none',
        alignItems: 'center',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    textButton: {
        color: 'white',
        textDecorationLine: 'none',
    }
});

class Navbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            openM: false,
            anchor: null
        }
        this.openDrawer = this.openDrawer.bind(this);
        this.openMenu = this.openMenu.bind(this);
        
    }

    openDrawer(){this.props.open('drawer');}

    openMenu(event){
        this.props.open_menu();
        this.props.change_menu(['anchorEl',event.currentTarget])
        
    }
    // open Dialog Information
    openInfo(){
        this.props.openDI('open')
    }
    

    render() {
       
        

        const { classes } = this.props;
        //const { Drawer } = this.props;
        
        return (
            <div className="grow">
                <AppBar position="static" color ='primary'>     
                    <Toolbar>
                        <IconButton
                            edge="start"
                            className={classes.menuButton}
                            color="inherit"
                            aria-label="open drawer"
                            onClick={this.openDrawer}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Typography variant="h4" noWrap>IMA</Typography>
                        <div className={classes.grow}/>
                        <div className={classes.sectionDesktop}>
                            <div className="icon_nav">
                                <Tooltip title="Informacin">
                                    
                                    <IconButton 
                                        aria-label="show 1 new mails" 
                                        color="inherit"
                                        onClick={this.openInfo.bind(this)}
                                        >
                                        <img alt='log-info' src={Search} height="30px"></img>
                                    </IconButton>
                                </Tooltip>
                                
                                
                                <Tooltip title="Notificaciones">
                                    <IconButton aria-label="show 2 new notifications" color="inherit" >
                                        <Badge badgeContent={0} color="secondary">
                                            <NotificationsIcon />
                                        </Badge>
                                    </IconButton>
                                </Tooltip>
                                
                            </div>
                            <IconButton
                                aria-label="account of current user"
                                color="inherit"
                                onClick={this.openMenu.bind(this)}
                            >
                                <AccountCircle style={{fontSize: '35px'}}/>
                            </IconButton>
                        </div>
                        <div className="section-mobile"></div>
                    </Toolbar>
                </AppBar>
                <MenuNav />
            </div>
            
        );
    }
}


const mapStateToProps = (state) => {
    return {
        Drawer: state.Drawer
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
        change: (text) => {dispatch(change(text))},
        change_menu: (text) => {dispatch(change_menu(text))},
        open: (text) => {dispatch(open_drawer(text))},
        open_menu: (text) => {dispatch(open_menu(text))},
        openDI: (text) => {dispatch(openDI(text))},
       // change the state of authSuccess
       
    }
}
export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Navbar)));

