import React,{Component}   from 'react';
import '../css/dialogInformation.css';

import AddIcon from '@material-ui/icons/Add';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import Fab from '@material-ui/core/Fab';
import { Grid } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search';
import Tooltip from '@material-ui/core/Tooltip';


import {connect} from 'react-redux'
import {openDI} from '../_actions/Dialog'


class DialogInformation extends Component{
    constructor(props) {
        super(props)
        this.state = {}
    }
    closeDialog(){
        this.props.openDI('close')
    }
 
    render(){
        let {dialog} = this.props

        return(
            <Dialog open={dialog.openI}>
                <DialogTitle >
                    <Grid container direction="row">
                        <Grid item xs={6} md={6} className="dialogInfo_text">
                            {"Informacin"}
                        </Grid>
                        <Grid item xs={2} md={2}></Grid>
                        <Grid item xs={4} md={4} align="right">
                            <Tooltip title="Cerrar men">
                                <Fab color="primary" size="small" >
                                    <CloseIcon onClick={this.closeDialog.bind(this)}/>
                                </Fab>
                            </Tooltip>
                            
                        </Grid>
                    </Grid>
                </DialogTitle>
                <DialogContent>
                    {
                        localStorage.getItem('view') === 'services' ? 
                        <Grid container direction="row" alignItems="center">
                            <Grid item xs={10} md={10}>
                                <p className="dialogInfo_text">
                                    Bienvenido al portal de creacin de servicios,
                                    al salir de este men podra encontrar un boton de +
                                    con el cual podra registrar sus servicios.
                                </p>        
                            </Grid>
                            <Grid item xs={2} md={2} align="center">
                                <Fab color="secondary" size="medium"  >
                                    <AddIcon style={{ fontSize: 30 }}/>
                                </Fab>
                            </Grid>
                        </Grid>
                        :
                        <Grid container direction="row" alignItems="center">
                            <Grid item xs={10} md={10} align="center">
                                <p className="dialogInfo_text">
                                    Bienvenido al portal, para buscar un nuevo servicio presione 
                                </p>        
                            </Grid>
                            <Grid item xs={2} md={2} align="center">
                                <MenuIcon style={{ fontSize: 32 }} />       
                            </Grid>
                            <Grid item xs={10} md={12}>
                                <p className="dialogInfo_text">
                                    A continuacin encontrar un men, ahi podr seleccionar
                                    el origen y destino
                                </p>        
                            </Grid>
                            <Grid item xs={10} md={10}>
                                <p className="dialogInfo_text">
                                    Finalmente para obtener las alternativas
                                    debe presionar
                                    
                                </p>        
                            </Grid>
                            <Grid item xs={2} md={2} align="center">
                                <SearchIcon style={{ fontSize: 34 }}/>      
                            </Grid>
                        </Grid>
                    }
                    
                </DialogContent>                
            </Dialog>
        );
    }
}

const mapStateToProps = (state) => {
    return {
       dialog: state.Dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
       openDI: (date) => {dispatch(openDI(date))},
    }
}
 
export default connect(mapStateToProps,mapDispatchToProps)(DialogInformation);