import React,{Component}   from 'react';
import '../css/back_drop.css'

import Backdrop from '@material-ui/core/Backdrop';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid'
import withStyles from '@material-ui/core/styles/withStyles';


import {connect} from 'react-redux'
import {openBD} from '../_actions/actions_backdrop'

const styles = theme => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
      fontSize: '40px'
    },
    message:{
        paddingBottom: '60px',
        marginTop: '-110px',
    }
});

class BackDrop extends Component{
    constructor(props) {
        super(props)
        this.state = {
            open: true,
            success: true,
        }
    }
 
    handleClose(){
        this.setState({open: false})
    }
    render(){
        const { classes } = this.props;
        const { back_drop } = this.props;
        return(
            <div>
                <Backdrop className={classes.backdrop} open={back_drop.open}>
                    <Grid container direction="row">
                        {   !this.props.complete ? 
                            <Grid container direction="row">
                                <Grid item xs={12} sm={12} align="center" className={classes.message}>
                                    {this.props.message}
                                </Grid>
                                <Grid item xs={12} sm={12} align="center">
                                    <CircularProgress size={150} color="primary"/>
                                </Grid>
                            </Grid>
                        :   
                            <Grid container direction="row">
                                <Grid item xs={12} sm={12} align="center" className={classes.message}>
                                    {this.props.message}
                                </Grid>
                                <Grid item xs={12} sm={12} align="center">
                                    <CheckCircleIcon style={{fontSize: '220px', color: '#31a361'}}/>
                                </Grid>
                            </Grid>
                        }
                    </Grid>
                </Backdrop>
            </div>
        );
    }
}



const mapStateToProps = (state) => {
    return {
        back_drop: state.BackDrop
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       open: (text) => {dispatch(openBD(text))},
    }
}
 
export default withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(BackDrop));