import React, { Component } from 'react'
import '../css/user.css';

//import { CssBaseline, Grid} from '@material-ui/core';
import AirplanemodeActiveIcon from '@material-ui/icons/AirplanemodeActive';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import DirectionsBoatIcon from '@material-ui/icons/DirectionsBoat';
import DirectionsBusIcon from '@material-ui/icons/DirectionsBus';
import Divider from '@material-ui/core/Divider';
import Fab from '@material-ui/core/Fab';
import {Grid} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton'
import ImportExportIcon from '@material-ui/icons/ImportExport';
import ListCities from '../components/ListCities'
import SearchIcon from '@material-ui/icons/Search';
import Tooltip from '@material-ui/core/Tooltip';
import TrainIcon from '@material-ui/icons/Train';

import {connect} from 'react-redux'
import {open_drawer} from '../_actions/Drawer'
import {change_dialog} from '../_actions/Dialog'

//import Navbar from '../components/NavUser'


class DrawerUser extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
        this.handleClose = this.handleClose.bind(this);
    }

    handleClose(){
        this.props.open('drawer')
    }
    changeValues(){
        let origin = this.props.dialog.origin
        let destination = this.props.dialog.destination
        this.props.change_dialog(["Origen",destination]);
        this.props.change_dialog(["Destino",origin]);
        //console.log( this.props.dialog.origin,this.props.dialog.destination)
    }

    render(){
        //const {dialog} = this.props
        return(
            <div>
                <Grid container direction="row">
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen}>
                            <TrainIcon style={{fontSize: '30px'}} color={'secondary'} />
                        </IconButton>
                    </Grid>
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton  className="button" onClick={this.handleDrawerOpen} disabled={true}>
                            <DirectionsBoatIcon color={'inherit'} />
                        </IconButton>
                    </Grid>
                    
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen} disabled={true}>
                            <AirplanemodeActiveIcon color={'inherit'}/>
                        </IconButton>
                    </Grid>
                    <Grid item xs={3} md={3} className="drawerHeader">
                        <IconButton className="button" onClick={this.handleDrawerOpen} disabled={true}>
                            <DirectionsBusIcon color={'inherit'}/>
                        </IconButton>
                    </Grid>
                </Grid>

                <Grid container spacing={4} justify="center" alignItems="center" className='drawerH'>

                    <Grid  item xs={5} md={5} align="center">
                        <ListCities type="Origen" color="#81ABFF"/>
                    </Grid>
                    <Grid item xs={5} md={5} align="center">
                        <ListCities type="Destino" color="#81ABFF"/>
                    </Grid>

                    <Grid item xs={3} md={3} align="center">
                        
                        <Tooltip title="Cambiar valores">
                            <Fab color="secondary" size={'small'}>
                                <ImportExportIcon onClick={this.changeValues.bind(this)}/> 
                            </Fab>
                        </Tooltip>
                    </Grid>
                    <Grid item xs={3} md={3} align="center">
                        <Tooltip title="Buscar">
                            <Fab color="secondary" size={'large'} disabled={true}>
                                <SearchIcon  style={{fontSize: '35px'}}/> 
                            </Fab>
                        </Tooltip>
                    </Grid>
                    <Grid item xs={3} md={3} align="center">
                        <Tooltip title="Cerrar drawer">
                            <Fab color="secondary" size={'small'}>
                                    <ChevronLeftIcon onClick={this.handleClose}/>
                                </Fab>
                        </Tooltip>
                    </Grid>

                </Grid>
                <Divider/>
                <Grid item md={12}>
                    {this.props.cards}
                </Grid>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        Drawer: state.Drawer,
        dialog: state.Dialog
    };
 };
 
const mapDispatchToProps = (dispatch) => {
    return{
       open: (text) => {dispatch(open_drawer(text))},
       change_dialog: (value) => {dispatch(change_dialog(value))},
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(DrawerUser);