import React, { Component } from 'react'
import '../css/user.css';

//import auth from '../router/Auth'
import Card from '@material-ui/core/Card';
//import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { CssBaseline, Grid} from '@material-ui/core';
import DialogInfo from '../components/DialogInformation'
import Drawer from '@material-ui/core/Drawer';
import DrawerUser from '../components/DrawerUser';
import ListInfo from '../components/ListInfo'
import Navbar from '../components/NavUser'
import SnackUser from '../components/SnackUser'
import store from '../store'
import Typography from '@material-ui/core/Typography'
import { withRouter } from 'react-router-dom';

import {connect} from 'react-redux'
import {open_drawer,loadA} from '../_actions/Drawer';
import {openDI} from '../_actions/Dialog';

//var wtf8 = require('wtf-8');

class User extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
            months:{
                'Mar':'Marzo',
                'Apr':'Abril',
            },
            
        }
    }

    componentDidMount(){ 
        if (localStorage.getItem('view') === 'services'){
            this.props.history.push('/forbidden')
        }
        this.props.openDI('open')
        this.props.loadA('load data')  
    }

    render(){
        
        // use to load alternatives
        const {drawer} = this.props

        // use to load services
        const {dialog} = this.props
        // use to load users
        const {register} = this.props
        
        
        //let cards = drawer.alternatives.map(card =>{
        let cards = dialog.cards.map(card =>{
            //console.log(dialog.origin ,"+ >", card.origin)
            //console.log(dialog.destination ,"+ <", card.destination)
            if(dialog.origin === card.origin && dialog.destination === card.destination){
                return(
                    <Card key={card.id}>
                        <CardContent>
                            <Grid container direction="row" >
                                <Grid item xs={9} md={9} align="left" className="cardUser_text">
                                    <b>{card.title.toUpperCase()}</b>
                                </Grid>
                                <Grid item xs={3} md={3} align="right">
                                    <Typography gutterBottom  color="primary" component="h4">
                                    {card.date_start.slice(4,10)} - {card.date_end.slice(4,10)} 
                                    </Typography>
                                </Grid>

                                <Grid item xs={9} md={9} align="left" className="cardUser_text">
                                    <p>
                                    A continuacin encontrara informacin detallada del servicio
                                    asi como los datos del transportador
                                    </p>
                                </Grid>
                                <Grid item xs={3} md={3} align="right">
                                    <Typography gutterBottom  color="primary" component="h4">
                                    {24 * (parseInt(card.date_end.slice(8,10))-parseInt(card.date_start.slice(8,10)))+ ' horas'} 
                                    </Typography>
                                </Grid>
    
                                <Grid item xs={8} md={8} align="left" className="cardUser_text"/>
                                <Grid item xs={4} md={4} align="right">
                                    843 km
                                </Grid>
                                <Grid item xs={12} md={12}>
                                    <ListInfo type="Detalles" id={card.id} id_user={card.id_user}/>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>    
                )   
            }
            else{return(<div></div>)}
        })

        return(
            <div className="root_user">
                <Grid container component="main" className="pater" direction="row">
                    <Grid item xs={12} >
                        <Navbar/>
                        <CssBaseline/>
                    </Grid>
                    <Grid item xs={12}></Grid>
                        <Drawer
                            className="drawer"
                            variant="persistent"
                            anchor="left"
                            open={store.getState().Drawer.open}
                            classes={{paper: "drawerPaper"}}
                        ><DrawerUser cards={cards}/>
                        <DialogInfo/>
                    </Drawer>
                    <div className="user_image"/>
                    <SnackUser message={register.nombre}/>
                </Grid>
                    
            </div>
            
        )
    }
}

const mapStateToProps = (state) => {
    return {
        drawer: state.Drawer,
        dialog: state.Dialog,
        register: state.Register
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        loadA: (text) => {dispatch(loadA(text))},
        open_drawer: (text) => {dispatch(open_drawer(text))},
        openDI: (text) => {dispatch(openDI(text))},
       

    }
}
export default withRouter(connect(mapStateToProps,mapDispatchToProps)(User))